//分享处理函数
export function lkt_showShareMask (me) {























    me.shareMask = true

    // console.log('title是', me.order.title)
    var data = {
        module: 'app',
        action: 'getcode',
        m: 'share',
        proId: me.order.product_id,
        type: 'JP',
        bindding_id: me.bindding_id
    }
    me.$req.post({data}).then(res => {
        if (res.code == 200) {
            me.ewmImg = uni.getStorageSync('endurl') + res.imgUrl
        } else if (res.code == 404) {
            uni.showToast({
                title: me.language.laiketuiCommon.noLogin,
                icon: 'none',
                duration: 1500
            })
            setTimeout(function () {
                uni.navigateTo({
                    url: '../../pages/login/login?landing_code=1'
                })
            }, 1000)
        } else {
            uni.showToast({
                title: res.message,
                duration: 1500,
                icon: 'none'
            })
        }
    })

}

//分享处理函数
export function lkt_showSaveEWM (string, me) {

    var data = {
        module: 'app',
        action: 'getcode',
        m: 'share',
        proId: me.order.product_id,
        type: 'JP',
        bindding_id: me.bindding_id
    }
    me.$req.post({data}).then(res => {
        if (res.code == 200) {
            me.ewmImg = uni.getStorageSync('endurl') + res.imgUrl
            me.saveEWM = true
            me.shareMask = true
            // console.log('图片路径' + me.ewmImg)
        } else if (res.code == 404) {
            uni.showToast({
                title: me.language.laiketuiCommon.noLogin,
                icon: 'none',
                duration: 1500
            })
            setTimeout(function () {
                uni.navigateTo({
                    url: '../../pages/login/login?landing_code=1'
                })
            }, 1000)
        } else {
            uni.showToast({
                title: res.message,
                duration: 1500,
                icon: 'none'
            })
        }
    })

}

