export async function lkt_pay_wx (type, me) {
    if (!type) {
        return
    }
    let orderInfo = await me.getOrderInfo(type)
    var providerStr = ''
    if (type == 'wx') {
        providerStr = 'wxpay'
    } else if (type == 'ali') {
        providerStr = 'alipay'
    } else if (type == 'baidu') {
        providerStr = 'baidu_pay'
    }

    if (orderInfo.statusCode !== 200) {
        me._payFail()
    }
    var str = JSON.stringify(orderInfo.data)
    uni.hideLoading()


























































































































    uni.requestPayment({
        provider: providerStr,
        orderInfo: orderInfo.data, //订单数据
        success: function (res) {
            console.log('--------------payok------------')
            console.log(res)








            uni.showToast({
                title: me.language.toasts.bidding.payOk,
                duration: 1000,
                icon: 'none'
            })
            me.$store.state.payRes = me.order_list
            me.pay_display = false
            uni.showToast({
                title: me.language.toasts.bidding.yjOk,
                duration: 1000,
                icon: 'none'
            })
            uni.redirectTo({
                url: './bidding_detailed_Two?mark=1&detail_one=1',

            })
            return false
        },
        fail: function (err) {
            console.log('--------------payfail------------')
            console.log(err)
            var str = JSON.stringify(err)
            me._payFail()
        }
    })


















































}

